import { computed } from 'vue';
import type { UserStatusEnum } from '@/types';
import { useIndexesStore, useStatusStore, useUserStore } from '@/stores';
import ContactIcon from '@/assets/images/icon-contacts.svg';

interface Props {
  me: boolean;
  srcUser: number | null;
}

export function useAvatar({ me, srcUser }: Props) {
  const statusStore = useStatusStore();
  const indexesStore = useIndexesStore();
  const userStore = useUserStore();

  const avatar = computed(() => {
    const sizeImage = 32;

    if (!srcUser) {
      return {
        status: undefined,
        src: ContactIcon,
      };
    }

    if (!me) {
      return {
        status:
          (statusStore?.status[srcUser]?.status as unknown as UserStatusEnum) ??
          'offline',
        src: `${indexesStore.avatars[srcUser][sizeImage]}`,
      };
    }

    return {
      status: userStore.onlineStatus,
      src: `${indexesStore.avatars[userStore.user.userId][sizeImage]}`,
    };
  });

  return {
    avatar,
  };
}
